import { motion } from "framer-motion";
import styles from "@/app/styles/componentStyles/main/greeting.module.";
import { useTranslations } from "next-intl";

export default function Greeting({ onClick }: () => void) {
  const t = useTranslations();

  const title = t("translation.greeting.title");
  const instruction = t("translation.greeting.instruction");

  return (
    <div className={styles.container}>
      <motion.h1
        className={styles.greeting}
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 2 }}
      >
        {title}
      </motion.h1>

      <motion.img
        onClick={onClick}
        initial={{
          filter: ["brightness(50%) contrast(60%)", "brightness(40%) contrast(100%)"],
        }}
        whileTap={{
          x: [0, -2, 2, -2, 2, 0],
          rotate: [0, -1, 1, -1, 1, 0],
          filter: ["brightness(50%) contrast(60%)", "brightness(150%) contrast(250%)"],
          transition: { repeat: Infinity, duration: 0.5, repeatType: "loop" },
        }}
        className={styles.lamp}
        src="/images/light bulb.svg"
        alt="lamp"
      />

      <motion.p
        className={styles.instruction}
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 2, duration: 1 }}
      >
        {instruction}
      </motion.p>
    </div>
  );
}
